<template>
	<view class="lazy-image-container">
		<image 
			v-if="isLoaded || isInViewport"
			:src="src" 
			:mode="mode"
			:class="imageClass"
			:style="imageStyle"
			@load="onImageLoad"
			@error="onImageError"
		></image>
		<view 
			v-else 
			class="image-placeholder"
			:class="placeholderClass"
			:style="placeholderStyle"
		>
			<u-loading-icon mode="spinner" size="24"></u-loading-icon>
		</view>
	</view>
</template>

<script>
export default {
	name: 'LazyImage',
	props: {
		src: {
			type: String,
			required: true
		},
		mode: {
			type: String,
			default: 'aspectFill'
		},
		imageClass: {
			type: String,
			default: ''
		},
		imageStyle: {
			type: Object,
			default: () => ({})
		},
		placeholderClass: {
			type: String,
			default: ''
		},
		placeholderStyle: {
			type: Object,
			default: () => ({})
		},
		threshold: {
			type: Number,
			default: 100
		}
	},
	data() {
		return {
			isLoaded: false,
			isInViewport: false,
			observer: null
		}
	},
	mounted() {
		this.initIntersectionObserver();
	},
	beforeDestroy() {
		if (this.observer) {
			this.observer.disconnect();
		}
	},
	methods: {
		initIntersectionObserver() {
			// 使用 Intersection Observer API 检测元素是否进入视口
			if (typeof IntersectionObserver !== 'undefined') {
				this.observer = new IntersectionObserver(
					(entries) => {
						entries.forEach(entry => {
							if (entry.isIntersecting) {
								this.isInViewport = true;
								this.observer.unobserve(entry.target);
							}
						});
					},
					{
						rootMargin: `${this.threshold}px`
					}
				);
				
				this.$nextTick(() => {
					const el = this.$el;
					if (el) {
						this.observer.observe(el);
					}
				});
			} else {
				// 降级方案：直接加载
				this.isInViewport = true;
			}
		},
		onImageLoad() {
			this.isLoaded = true;
			this.$emit('load');
		},
		onImageError() {
			this.isLoaded = true;
			this.$emit('error');
		}
	}
}
</script>

<style lang="scss" scoped>
.lazy-image-container {
	position: relative;
	overflow: hidden;
	
	.image-placeholder {
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #f5f5f5;
		color: #999;
		font-size: 24rpx;
	}
}
</style> 